@import "@/assets/global.scss";














































































































.main-content {
  padding-top: 254px;
}

.header {
  position: fixed;
  z-index: 10;
  top: 52px;
  background-color: #FAFAFA;
}

.article {
  width: 320px;
  margin: 16px auto;
  display: flex;
  justify-content: space-between;

  > span {
    display: flex;
    align-items: center;
  }
}

.labelList {
  width: 320px;
  margin: 0 auto;
  max-height: 48px;
  overflow-y: hidden;
  box-sizing: border-box;
  transition: max-height 0.5s ease-in-out;
}

.expandList {
  max-height: 500px;
}

.bar {
  width: 100vw;
  border: 1px solid #E9E9E8;
}

.link {
  margin-bottom: 32px;
}
